.logo {
  height: 64px;
  position: relative;
  line-height: 64px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  overflow: hidden;
}

.logo .logo-title {
  color: rgba(255, 255, 255, 0.85);
}
.logo h1 {
  display: inline-block;
  vertical-align: middle;
  font-size: 20px;
  margin: 0 0 0 12px;
  font-family: 'Myriad Pro', 'Helvetica Neue', Arial, Helvetica, sans-serif;
  font-weight: 400;
}

.logo img {
  display: inline-block;
  vertical-align: middle;
  height: 32px;
}

@navbar-height: 64px;
@footer-height: 40px;
@sidebar-width: 200px;
@aside-menu-width: 250px;
@zindex-sticky: 1020;

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  //background-color: #f4f6f8;
  background-color: #ecf0f5;
  height: 100%;
}

a {
  text-decoration: none;
}

#root {
  height: 100%;
}

@media all and (-ms-high-contrast: none) {
  html {
    display: flex;
    flex-direction: column;
  }
}

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.app-header {
  //color: #ffffff;
  background-color: #3c8dbc;
  //background-color: #001529;
  flex: 0 0 @navbar-height;
  //display: flex;
  //align-items: center;
  // border-bottom: 1px solid #c8ced3;
  padding: 0 1rem;
  position: relative;
  &::after{
    content: '';
    position: absolute;
    height: 1px;
    width: 100%;
    left: 0;
    bottom: 0;
    background-color: #c8ced3;
  }
}

.app-body {
  display: flex;
  flex-direction: row;
  flex-grow: 1;
  overflow-x: hidden;

  .main {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    height: calc(100vh - @navbar-height);
    overflow-y: auto;

    .main-content {
      flex-grow: 1;
    }

    .main-footer {
      //border-top: 1px solid #c8ced3;
      background-color: #f0f3f5;
      display: flex;
      align-items: center;
      //height: @footer-height;
      flex: 0 0 @footer-height;
      //padding-left: 1em;
      justify-content: center;
    }
  }

  .sidebar {
    // @sidebar-width is the width of the columns
    flex: 0 0 @sidebar-width;
    height: calc(100vh - @navbar-height);
    background: #fff;
    //background: #333;
    //background: #000c17;
    //background: #001529;
    //background-image: url("https://demo.dashboardpack.com/architectui-react-free/static/media/city1.ebc5562d.jpg");
    // put the nav on the left
    //border-right: 1px solid #c8ced3;
    order: -1;
    overflow-y: auto;
  }
}

@primary-color: #20a8d8;